.slider {
   
    position: relative;
    &__slide {
 
        min-height:100px;
       }
       &__arrow { 
  
        position: absolute;
        top: 0;
        bottom: 0;  
        padding: 20px;
        background: none;
        border: none;
        color: $primary-color;
        font-size: 2em;
        outline: none;
        &:hover,
        &:active,
        &:focus {
        transform: scale(1.2);
        cursor: pointer;
}

        &--prev { 
            left: 0;
            i{
                display: inline-block;
                margin-left: -3px;
            }
        }
        &--next {
            right: 0;
            i{
                display: inline-block;
                margin-left: -3px;
            }
         }
    }
    &__dots {  
 
        text-align: center;
        padding: 40px 0;
        margin: 0;
        li{
            display: inline-block;
        }
       
    }
    &__dot{
        display: inline-block;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: $primary-color;
        border: $border-width solid $secondary-color ;
        outline: none;
        &:hover,
        &:active,
        &:focus,
        &--active {
            cursor: pointer;
        background-color:$secondary-color ;
        border-color: $primary-color ;
}
}
}









